import React, {Component} from 'react';
import {connect} from 'react-redux';
import {fetchPosts, fetchUsers} from '../actions/index';
import {Link} from 'react-router';

class PostsIndex extends Component {
    componentWillMount() {
        this.props.fetchPosts();
        this.props.fetchUsers();

    }

    renderPosts() {
        return this.props.posts.map((post) => {
            return (
                <li className="list-group-item" key={post.id}>
                    <Link to={"posts/" + post.id}>
                        <span className="pull-xs-right">{post.categories}</span>
                        <strong>{post.title}</strong>
                    </Link>
                </li>
            );
        });

    }
    renderUser(user) {
        return (
            <div className="card card-block" key={user.id}>
                <h4 className="card-title">{user.name}</h4>
                <p className="card-text">{user.company.name}</p>
                <a className="btn btn-primary" href={user.website}>Website</a>
            </div>
        );
    }
    render() {
        return (
            <div>
                <div className="text-xs-right">
                    <Link to="/posts/new" className="btn btn-primary">
                        Add a Post
                    </Link>
                </div>
                <h3>Posts</h3>
                <ul className="list-group">
                    {this.renderPosts()}
                </ul>
                <div className="user-list">
                    {this.props.users.map(this.renderUser)}
                </div>
            </div>
        )
    }
}

function mapStateToProps(state) {
    return {posts: state.posts.all, users: state.users};
}

export default connect(mapStateToProps, {fetchPosts, fetchUsers})(PostsIndex);



